@import '../assets/variables';

.about-content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.about-content__div__1 {
  background-color: $color-primary-light;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-left: 5px solid $color-tertiary-light;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  color: $color-secondary-light;
  align-items: center;
  min-height: 90vh;

  &:hover::before {
    transform: translateX(0);
  }

  &.dark {
    background-color: $color-primary-dark;
    color: $color-secondary-dark;
    border-left: 5px solid $color-tertiary-dark;
  }

  p {
    font-size: 2vw; // Default for large screens
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    padding-left: 50px;
  }

  @media (max-width: 768px) {
    p {
      font-size: 4vw; // Font size for tablet screens
      padding-left: 20px;
    }
  }

  @media (max-width: 480px) {
    p {
      font-size: 3.5vw; // Font size for phone screens
      padding-left: 20px;
      font-weight: 200;
    }
  }
}

.about-text__1p {
  font-size: $font-size-medium;
  line-height: 1.6;
  padding-top: 20px;
  animation: fadeIn 1.5s ease-in-out;

  &.dark {
    color: $color-secondary-dark;
  }
}

.about-content__div__2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-left: 5px solid $color-tertiary-light;
  position: relative;
  overflow: hidden;
  background-color: $color-primary-light;
  margin-top: 30px;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform $transition-speed ease-in-out;
  width: 100%;

  &:hover {
    transform: translateY(-5px);
    
  }

  &.dark {
    background-color: $color-primary-dark;
    color: $color-secondary-dark;
    border-left: 5px solid $color-tertiary-dark;
  }

  &__p {
    font-size: $font-size-large;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    margin: 10px;
    padding: 50px;
    animation: fadeIn 1.5s ease-in-out;
    color: $color-secondary-light;
    text-align: center;

    &.dark {
      color: $color-secondary-dark;
    }
  }

  @media (max-width: 480px) {
    &__p {
      font-size: $font-size-medium;
      margin: 0;
      padding: 0;
    }
  }

  @media (max-width: 768px) {
    margin-top: 20px;
    padding: 20px;
  }
}

.about__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    flex-wrap: nowrap;
  }
}

.projects {
  border-radius: 10px;
  overflow: hidden;
  width: 95%;
  margin: 20px;
  transition: transform $transition-speed ease;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  &:hover {
    transform: scale(1.05);
  }

  &:hover .project-title-hover {
    color: $color-tertiary-light;
  }

  @media (max-width: 768px) {
    width: 100%;
  }

  &.dark {
    box-shadow: rgba(255, 255, 255, 0.24) 0px 3px 8px;

    &:hover .project-title-hover {
      color: $color-tertiary-dark;
    }
  }
}


img {
  width: 100%;
  height: auto;
  // border-radius: 10px;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.05);
  }
}

.about__img {
  // mix-blend-mode: multiply;
  width: 30%;
  margin-left: 20px;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 1);
  margin: 0 20px 20px 20px; /* Adjust margins as needed */
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  &.dark {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    filter: brightness(0.7) contrast(1.2); // Slightly dim the image and increase contrast for dark theme

  }

  @media (max-width: 768px) {
    width: 80%; /* Adjusted width for phone screens */
    margin: auto;
  }
}

.about__intro {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.about__img.animated {
  animation: slideInFromLeft 1s ease forwards;
}

.about-text__animated {
  animation: slideInFromRight 1s ease forwards;
}

.about__img {

}

.about-text {
  opacity: 0;
  transform: translateX(100%);
  transition: transform 0.3s ease;

  &.dark {
    color: $color-secondary-dark;
  }
}

.underLine {
  border-bottom: 5px solid $color-tertiary-light;
  margin: 5px auto;
  width: fit-content;
  padding-bottom: 5px;

  &.dark {
    border-bottom: 5px solid $color-tertiary-dark;
  }
}

.responsive-img {
  content: url('../../assets/HomeImg.png'); /* Default small  */
}

@media (min-width: 768px) {
  .responsive-img {
    content: url('../../assets/HomeImg.png'); /* Large screen  */
  }
}
