@import '../assets/variables';

.subTitleContainer {
  background-color: $color-tertiary-light;
  color: $color-secondary-light;
  border-radius: 10px;
  height: 70px;
  width: auto;
  display: flex; 
  align-items: center;
  margin-bottom: 25px;
  margin-left: 10px;
  transition: background-color $transition-speed, color $transition-speed;

  &__h2 {
    white-space: nowrap;
    overflow: hidden;
    // border-right: .15em solid $color-tertiary-light; 
    animation: typing 5s steps(400) forwards;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 2vw; /* Default for large screens */
  }

  &.dark {
    background-color: $color-tertiary-dark;
    color: $color-secondary-dark;

    &__h2 {
      border-right: .15em solid $color-tertiary-dark;
    }
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* Media Queries for Responsive Font Sizes */
@media (max-width: 768px) {
  .subTitleContainer__h2 {
    font-size: 3vw;
  }
}

@media (max-width: 480px) {
  .subTitleContainer__h2 {
    font-size: 4vw;
  }
}
