@import '../assets/variables';

.footer {
    background: $color-footer-light;
    color: $color-secondary-light;
    text-align: center;
    padding: 20px;

    &.dark {
        background: $color-footer-dark;
        color: $color-secondary-dark;
    }

    .copyright,
    .contact,
    .social-links {
        margin-bottom: 10px;

        &.dark {
            color: $color-secondary-dark;
        }
    }

    a {
        color: $color-secondary-light;
        margin: 0 10px;
        text-decoration: none;

        &.dark {
            color: $color-secondary-dark;
        }

        &:hover {
            text-decoration: underline;
        }
    }
}
