// Light mode variables
$color-primary-light: #f2f2f2; 
$color-secondary-light: #555555; 
$color-tertiary-light: #6699cc; 
$color-hover-light: #88b4e7; 
$color-footer-light: #282c34; 
$font-size-large: 1.5rem;
$font-size-medium: 1rem;
$transition-speed: 0.3s;
$color-body-light: #ffffff; // Body color for light mode

// Dark mode variables
$color-primary-dark: #0d1117;      
$color-secondary-dark: #f0f6fc;  
$color-tertiary-dark: #58a6ff;    
$color-hover-dark: #f0f6fc;        
$color-footer-dark: #161b22;       
$color-root-dark: #0d1117;         
$color-body-dark: #303d55; // Body color for dark mode
