@import '../assets/variables';

.project-a {
  &__card {
    display: flex;
    flex-direction: row;
    background-color: $color-primary-light;
    border-radius: 20px;
    margin: 20px;
    padding: 20px;
    transition: transform 0.3s ease, background-color 0.3s ease;
    height: 80%;

    &:hover {
      transform: translateY(-2px);

    }

    &.dark {
      background-color: $color-primary-dark;
      &:hover{
        // .project-a__h3 {
        //   color: $color-hover-dark; // Change this to the desired color on hover
        // }
      }

    }
  }

  &__h3 {
    text-align: center;
    color: $color-secondary-light;
    font-family: "Montserrat", sans-serif;
    font-size: 2em;
    font-weight: 700;
    margin-bottom: 10px;

    &.dark {
      color: $color-secondary-dark;


    }
    

    @media (max-width: 768px) {
      margin: 0;
    }
  }

  &__h4 {
    text-align: center;
    font-size: $font-size-large;
    font-family: "Montserrat", sans-serif;
    color: $color-secondary-light;
    margin-bottom: 10px;

    &.dark {
      color: $color-secondary-dark;
    }
  }

  &__description {
    text-align: center;
    color: $color-secondary-light;
    font-family: "Montserrat", sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 1.5rem;
    margin-bottom: 20px;

    &.dark {
      color: $color-secondary-dark;
    }
  }

  &__img {
    border-radius: 20px;
    width: 50%;
    margin-right: 20px;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: scale(1.05) translateY(-2px);
      box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.3);
    }

    &.dark {
&:hover{
  box-shadow: 0px 5px 10px 3px rgba(255, 255, 255, 0.3);

}
    }
  }

  &__details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;

    &.dark {
      color: $color-secondary-dark;
    }
  }

  &__link {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: $color-secondary-light;
    transition: transform 0.3s ease, color 0.3s ease;
    border-radius: 10px;
    justify-content: center;
    padding: 10px;
    width: fit-content;

    &:hover {
      transform: scale(1.1);
      color: $color-tertiary-light;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
      
    }

    .gitLogo {
      height: 30px;
      width: 45px;
    }

    &.dark {
      color: $color-secondary-dark;
      filter: invert(0.4);
      

      &:hover {
        color: $color-tertiary-dark;
        box-shadow: 0px 5px 10px 3px rgba(255, 255, 255, 0.3);
      }
    }
  }

  &__tools {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;

    &.dark {
      color: $color-secondary-dark;
    }

    &--logos {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 10px;
      max-width: 100%;
      overflow: hidden;
    }

    .tool {
      display: flex;
      align-items: center;

      .tool-logo {
        width: 40px;
        height: 40px;
        transition: transform 0.3s ease;

        &:hover {
          transform: scale(1.2);
          
        }

        &.dark {
          filter: invert(0.4); // Adjust the filter for dark mode if needed
        }
      }
    }
  }
}

.modal-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: $color-secondary-light;
  border: none;
  color: #fff;
  font-size: 1.9rem;
  cursor: pointer;
  transition: transform 0.3s ease;
  z-index: 10;

  &:hover {
    transform: scale(1.2);
  }

  &.dark {
    // background: $color-secondary-dark;
  }
}

.carousel-container,
.carousel-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-image {
  max-width: 100%;
  max-height: 90vh;
  object-fit: contain;
  border-radius: 20px;
}

.carousel .slide,
.carousel .thumbs-wrapper,
.carousel .thumb {
  background: none;
  border: none;
  box-shadow: none;
  margin: 20px 0 0;
  padding: 0;
}

.carousel .control-dots .dot {
  background: $color-secondary-light;
  box-shadow: none;
  transition: opacity 0.25s ease-in;

  &.selected,
  &:hover {
    opacity: 1;
    transform: scale(1.2);
  }

  &.dark {
    background: $color-secondary-dark;
  }
}

.carousel .carousel-status {
  bottom: 10px;
  right: 10px;
  color: $color-secondary-light;
  font-size: 1.2rem;

  &.dark {
    color: $color-secondary-dark;
  }
}

.carousel .control-arrow,
.carousel.carousel-slider .control-arrow {
  font-size: 2.5rem;
  color: $color-secondary-light;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 10;
  transition: transform 0.3s ease, color 0.3s ease;

  &:hover {
    color: $color-tertiary-light;
    transform: scale(1.2);
  }

  &.dark {
    color: $color-secondary-dark;

    &:hover {
      color: $color-tertiary-dark;
    }
  }
}

@media (max-width: 768px) and (min-width: 480px) {
  .project-a__card {
    flex-direction: row;
  }

  .project-a__card:first-child,
  .project-a__card:last-child {
    flex-direction: column;
  }

  .project-a__card:nth-child(2),
  .project-a__card:nth-child(3) {
    flex-direction: row;
  }
}

@media (max-width: 768px) {
  .project-a__card {
    flex-direction: row;
    height: auto;
  }

  .project-a__img {
    margin-right: 0;
    width: auto;
    margin-bottom: 10px;
    margin-top: 10px;
  }

  .project-a__details {
    width: 100%;
  }

  .project-a__link .gitLogo {
    height: 20px;
    width: 30px;
  }

  .project-a__tools .tool .tool-logo {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 480px) {
  .project-a__link .gitLogo {
    height: 15px;
    width: 22.5px;
  }

  .project-a__tools .tool .tool-logo {
    width: 20px;
    height: 20px;
  }

  .project-a__tools {
    margin: 0;
  }

  .project-a__card {
    flex-direction: column;
    height: auto;
    margin: 0;
    padding: 0;
  }
}

.no-scroll {
  overflow: hidden;
}
