@import '../assets/variables';

#contact {
  background-color: $color-primary-light;
  color: $color-secondary-light;
  padding: 50px 0;
  border-left: 5px solid $color-tertiary-light;
  border-radius: 20px;
  margin-bottom: 20px;

  &.dark {
    background-color: $color-primary-dark;
    color: $color-secondary-dark;
    border-left: 5px solid $color-tertiary-dark;
  }

  .container {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
  }

  form {
    margin-top: 20px;
  }

  .form-group {
    margin-bottom: 20px;

    &.dark input[type="text"],
    &.dark input[type="email"],
    &.dark textarea {
      border: 1px solid $color-tertiary-dark;
      background-color: $color-primary-dark;
      color: $color-secondary-dark;
    }
  }

  label {
    display: block;
    font-size: 1.1rem;
    margin-bottom: 5px;

    &.dark {
      color: $color-secondary-dark;
    }
  }

  input[type="text"],
  input[type="email"],
  textarea {
    width: 90%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid $color-tertiary-light;
    border-radius: 5px;
  }

  textarea {
    resize: vertical;
  }

  button {
    background-color: $color-tertiary-light;
    color: $color-primary-light;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: darken($color-tertiary-light, 10%);
    }

    &.dark {
      background-color: $color-tertiary-dark;
      color: $color-primary-dark;

      &:hover {
        background-color: darken($color-tertiary-dark, 10%);
      }
    }
  }
}
