@import '../assets/variables';

.skills {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  height: 50vh;
  align-items: center;
  justify-content: center;
  border-left: 5px solid $color-tertiary-light;
  border-radius: 20px;
  background-color: $color-primary-light;
  padding: 50px;

  &.dark {
    background-color: $color-primary-dark;
    border-left: 5px solid $color-tertiary-dark;
  }

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.skills__div__1, .skills__div__2 {
  width: 100%;
  text-align: center;

  @media (min-width: 768px) {
    width: 50%;
  }

  &.dark {
    color: $color-secondary-dark;
  }
}

.Language__container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.sphere {
  position: relative;
  perspective: 1000px;
}

.language {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-style: preserve-3d;
}
